
import * as _ from 'lodash';

export function searchTagList(searchWord: string, tempList: any[]): any[] {

  if(searchWord == null || searchWord == '') {
    return _.clone(tempList);;
  }

  let tl: any[] = new Array();
      
  for(var i = 0 ; i < tempList.length; ++i) {

    if (tempList[i].tag.indexOf(searchWord) >= 0) {
      tl.push(tempList[i]);
    }
        

  }

  return tl;
}

export function searchWordsList(words: String[], tempList: any[]): any[] {

  if(tempList == null || tempList.length <= 0) {
    return null;
  }

  if (words.length <= 0) {
    return _.clone(tempList);;
  }

  let tl: any[] = _.clone(tempList);
  tl[0] = _.clone(tempList[0]);

  let newTagMap = new Map();

  tl[0].tagMap.forEach((value: any[], key: string) => {
    // if(value.root_title)
    // indexOf

    let searcharr: any[] = [];
    for (var i = 0; i < value.length; ++i) {
      let post = value[i];

      for (var j = 0; j < words.length; ++j) {
        if(words[j].length <= 0) {
          continue;
        }
        if (post.root_title.indexOf(words[j]) >= 0) {
          searcharr.push(post);
          break;
        }
      }


    }
    newTagMap.set(key, searcharr);
  });

  tl[0].tagMap = newTagMap;

  return tl;
}

export function searchList(searchWord: String, tempList: any[]): any[] {
    if(searchWord == null || searchWord == '') {
        return _.clone(tempList);;
      }
  
      
      let tl: any[] = _.clone(tempList);
      tl[0] = _.clone(tempList[0]);
      
      let newTagMap = new Map();
  
      tl[0].tagMap.forEach((value: any[], key: string) => {
        // if(value.root_title)
        // indexOf
  
        let searcharr: any[] = [];
        for(var i = 0 ; i < value.length; ++i) {
          let post = value[i];
          if(post.root_title.indexOf(searchWord) >= 0) {
            searcharr.push(post);
          }
        }
        newTagMap.set(key, searcharr);
      });
  
      tl[0].tagMap = newTagMap;

      return tl;
}

export interface MusicData {
  addr: string;
  type: string;
  post?: any;
}

export function searchAddress(post: any, mds: MusicData[]): Array<MusicData> {

  // let idx = -1;

  let addrs: Array<MusicData> = new Array();
 
  for(var i = 0 ; i < mds.length ; ++i) {
    
    let paList: Array<string> = parseAddr(post.body, mds[i].addr);

    for ( var j = 0 ; j < paList.length; ++j) {
      addrs.push({
        addr: paList[j],
        type: mds[i].type,
        post: post,
      });
    }

    // if(pa != null) {
      
    //   // console.log(pa);
    // }
  }

  return addrs;

}


function parseAddr(body: string, pattern: string): Array<string> {
  let startIdx: number = 0;

  let retArr: Array<string> = new Array();

  do {
    startIdx = body.indexOf(pattern, startIdx);

    if (startIdx < 0) {
     break;
    }

    let endIdx = checkEndIdx(body, startIdx);

    let pa: string;
    if(endIdx < 0) {
      pa = body.substring(startIdx);
    } else {
      pa = body.substring(startIdx, endIdx);
    }

    pa = processOtherPattern(pa);
    
    retArr.push(pa);

    if(endIdx > 0)  {
      startIdx = endIdx + 1;
    } else {
      startIdx = endIdx;
    }
    

  } while( startIdx >= 0);
  
  return retArr;
}

function processOtherPattern(pa: string): string {

  let lpIdx = pa.indexOf('/watch?v=')

  if(lpIdx > 0) {
    pa = processOtherPatternWatch(pa);
    return pa;
  }

  lpIdx = pa.indexOf('youtu.be/')

  if(lpIdx > 0) {
    pa = processOtherPatternShort(pa);
    return pa;
  }
  


  return pa;
}

function processOtherPatternWatch(pa: string): string {
  let lpIdx = pa.indexOf('/watch?v=')
  if(lpIdx > 0) {
    let yKey = pa.substring(lpIdx + '/watch?v='.length);

    yKey = removeGarbage(yKey);

    pa = 'https://www.youtube.com/embed/';
    pa += yKey;
    console.log(pa);
  }

  return pa;
}

function processOtherPatternShort(pa: string): string {
  let lpIdx = pa.indexOf('youtu.be/')
  if(lpIdx > 0) {
    let yKey = pa.substring(lpIdx + 'youtu.be/'.length);

    yKey = removeGarbage(yKey);

    pa = 'https://www.youtube.com/embed/';
    pa += yKey;
    console.log(pa);
  }

  return pa;
}

function removeGarbage(yKey: string): string {

  let garbageIdx = yKey.indexOf(')');
  if(garbageIdx > 0) {
    return yKey.substring(0, garbageIdx);
  }

  garbageIdx = yKey.indexOf('&t=');

  if(garbageIdx > 0) {
    return yKey.substring(0, garbageIdx);
  }

  return yKey;
}

function checkEndIdx(body: string, startIdx: number): number {

  // let enter: number = body.indexOf('\n', startIdx);

  // let space: number = body.indexOf(' ', startIdx);

  // let dq: number = body.indexOf('"', startIdx);

  // let checkArr: Array<number> = [enter, space, dq];

  let checkArr: Array<number> = new Array();

  checkArr.push(body.indexOf('\n', startIdx));
  checkArr.push(body.indexOf(' ', startIdx));
  checkArr.push(body.indexOf('"', startIdx));
  checkArr.push(body.indexOf('<br', startIdx));
  

  checkArr.sort((a: number, b: number) => {

    if ( a > b ) {
      return 1;
    } else {
      return -1;
    }
  });

  
  for ( var i = 0 ; i < checkArr.length ; ++i) {
    if ( checkArr[i] != -1) {
      return checkArr[i];
    }
  }


  return -1;
}


