import { Component, AfterContentInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { PostState, SelectState, ModeState } from '../../app/ng/reducers/states';
import { Observable } from 'rxjs/Observable';
import { PostInfo } from '../../app/ng/models/model';
import * as PostReducer from '../../app/ng/reducers/post.reducer';
import * as SelectReducer from '../../app/ng/reducers/select.reducer';
import * as fromActions from '../../app/ng/actions/actions';

import * as SearchUtil from '../../app/commons/SearchUtil';

@Component({
  selector: 'catch-card',
  templateUrl: 'catch.html',
  styleUrls: ['/src/pages/catch/catch.scss']
})
export class CatchPage implements AfterContentInit {
  selectedItem: any;
  icons: string[];
  items: Array<{title: string, note: string, icon: string}>;

  postInfoList: Observable<PostInfo[]>;
  selectTemp: Observable<String>;

  postList: PostInfo[] = [];
  selectTag: String = 'all_all';
  tempPostList: PostInfo[];

  constructor(public navCtrl: NavController, public navParams: NavParams,
     private postStore: Store<PostState>,
     private selectStore: Store<SelectState>,
     private modetore: Store<ModeState>) {
    // If we navigated to this page, we will have an item available as a nav param
    // this.selectedItem = navParams.get('item');

    if(this.postStore == null || this.postStore == undefined) {
      postStore = this.navParams.get('postStore');
      selectStore = this.navParams.get('selectStore');
    }

      // console.log(postStore);

    this.postInfoList = postStore.select(PostReducer.getPosts);
    this.selectTemp = selectStore.select(SelectReducer.getSelect);

    

    

  }

  optionSelected(event) {
    console.log('-=-=-=-=optionSelected=-=-=-=-=-=');
    console.log(event);
    this.modetore.dispatch(new fromActions.ModeAction(String(event)));
  }

  onCancel(event) {

    // console.log('-=-=-=-=onCancel=-=-=-=-=-=');
    // console.log(event);

  }

  ngAfterContentInit() {
    this.postInfoList.subscribe(
      (posts: PostInfo[]) => {
        // console.log(posts);
        // this.dataSource = new MatTableDataSource(probes);
        // this.dataSource.sort = this.sort;
        // console.log(ttt);
        this.tempPostList = posts;

        let ws: String[] = ["캐마", "캐치마인드", this.searchWord];

        this.postList = SearchUtil.searchWordsList(ws, this.tempPostList);
        this.postList = this.filterCatch(this.postList);
        // this.postList = SearchUtil.searchList(this.searchWord, this.tempPostList);;
        
        // if(posts.length > 0) {
        //   if(posts[0].postList.length > 0) {
        //     console.log(JSON.stringify(posts[0].postList[0]));
        //   }
          
        // }
        
      },
      (error: any) => {
        console.log(error.message);
      }
    );

    this.selectTemp.subscribe(
      (st: String) => {
        // console.log(posts);
        // this.dataSource = new MatTableDataSource(probes);
        // this.dataSource.sort = this.sort;
        console.log(st);
        this.selectTag = st;
        // console.log(this.selectTag);

        // let posts: PostInfo[] = [];
        // posts.push(this.postList[0]);
        // this.postStore.dispatch(new fromActions.PostAction(posts));
        // console.log(this.selectTag);
      },
      (error: any) => {
        console.log(error.message);
      }
    );
  }

  searchWord = '';

  getItems(ev) {

    this.searchWord = ev.target.value;

    this.postList = SearchUtil.searchList(ev.target.value, this.tempPostList);

  }

  filterCatch(posts: PostInfo[]): PostInfo[] {

    if(posts == null || posts.length <= 0) {
      return null;
    }

    posts[0].tagMap.forEach((value: any[], key: string) => {
      // if(value.root_title)
      // indexOf
  
      let searcharr: any[] = [];
      for (var i = 0; i < value.length; ++i) {
        let post = value[i];


        let aFIdx = post.body.indexOf('"정답:');
        if(aFIdx < 0) {
          continue;
        }

        let aLIdx = post.body.indexOf('"', '"정답:'.length + aFIdx);



        let answer: string = post.body.substring(aFIdx, aLIdx);

        let rFIdx = post.body.indexOf('"정답자:', aLIdx);
        if(rFIdx < 0) {
          continue;
        }

        let rLIdx = post.body.indexOf('"', '"정답자:'.length + rFIdx);

        let ra: string = post.body.substring(rFIdx, rLIdx);

        posts[0].catchMap.set(post.permlink, {answer:answer, righter:ra});

        console.log(posts[0].catchMap);
        

        // if (post.root_title.indexOf([j]) >= 0) {
        //   searcharr.push(post);
        //   break;
        // }

  
  
      }
      
    });
    

    return posts;
  }

}




