import { Component, ViewChild, AfterContentInit } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { PostInfo, AuthorInfo } from './ng/models/model';
import { Observable } from 'rxjs/Observable';
import { Store } from '@ngrx/store';
import { PostState, AuthorState, SelectState, GetEndState, ModeState } from './ng/reducers/states';

import * as PostReducer from './ng/reducers/post.reducer';
import * as AuthorReducer from './ng/reducers/author.reducer';
import * as GetEndReducer from './ng/reducers/getend.reducer';
import * as ModeReducer from './ng/reducers/mode.reducer';
import * as fromActions from './ng/actions/actions';
import { CardPage } from '../pages/card/card';
import { RealCardPage } from '../pages/real-card/real-card';
import { MusicPage } from '../pages/music/music';

import * as SearchUtil from '../app/commons/SearchUtil';
import { CatchPage } from '../pages/catch/catch';
// import * as steem from "steem";

@Component({
  templateUrl: 'app.html'
})
export class MyApp implements AfterContentInit {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = HomePage;

  pages: Array<{title: string, component: any}>;
  tempPages: Array<{title: string, component: any}>;

  selectTag: String = 'all_all';

  static DATE = new Date().toISOString().split('.')[0];
  static GET_SIZE = 10;

  postInfoList: Observable<PostInfo[]>;
  authorInfoList: Observable<AuthorInfo[]>;
  getEndFlag: Observable<Boolean>;
  modeFlag: Observable<String>;
  static spostStore: Store<PostState>;
  static sauthorStore: Store<AuthorState>;
  static sid: String;
  selectedPosts: Array<any> = new Array();

  postList: PostInfo[];

  searchTagWord: string = '';

  sortFlag = 0;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen,
    private postStore: Store<PostState>,
    private authorStore: Store<AuthorState>,
  private selectStore: Store<SelectState>,
  private getEndStore: Store<GetEndState>,
  private modetore: Store<ModeState>) {


      this.postInfoList = postStore.select(PostReducer.getPosts);
    this.authorInfoList = authorStore.select(AuthorReducer.getAuthor);
    this.getEndFlag = this.getEndStore.select(GetEndReducer.getGetEnd);
    MyApp.sauthorStore = this.authorStore;
    MyApp.spostStore = this.postStore;
    this.modeFlag = this.modetore.select(ModeReducer.getMode);

    this.initializeApp();

    // used for an example of ngFor and navigation
    // this.pages = [
    //   { title: 'Home', component: HomePage },
    //   { title: 'List', component: ListPage }
    // ];

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  ngAfterContentInit() {


    this.authorInfoList.subscribe(
      (authors: AuthorInfo[]) => {
        console.log(authors);
        // this.dataSource = new MatTableDataSource(probes);
        // this.dataSource.sort = this.sort;
      },
      (error: any) => {
        console.log(error.message);
      }
    );

    this.getEndFlag.subscribe(
      (getEnd: Boolean) => {
        console.log("END - " + getEnd);
        if(getEnd) {
          this.nav.setRoot(ListPage);
        }
      }
    );

    this.modeFlag.subscribe(
      (mode: String) => {
        console.log("this mode - " + mode);
         if(mode == "image") {
          console.log("mode - image")
          this.nav.setRoot(CardPage);
        } else if(mode == "card") {
          console.log("mode - card")
          this.nav.setRoot(RealCardPage);
        } else if(mode == "list") {
          console.log("mode - list")
          this.nav.setRoot(ListPage);
        } else if(mode == "music") {
          console.log("mode - music")
          this.nav.setRoot(MusicPage);
        }
      }
    );

    this.postInfoList.subscribe(
      (posts: PostInfo[]) => {
        
        if(posts.length <= 0) return;
        // this.postList = posts;
        // console.log('----------------------');
        let tpages: Array<any> = new Array();

        let postOne: PostInfo = posts[0];

        for(var i = 0 ; i < postOne.tagList.length; ++i) {
          var tagName: String = postOne.tagList[i];
          tpages.push({ tag: tagName, count : postOne.tagMap.get(tagName.toString()).length, component: ListPage });
        }

        this.tempPages = tpages;

        this.pages = SearchUtil.searchTagList(this.searchTagWord, this.tempPages);

        this.realTagSort(this.sortFlag);

      },
      (error: any) => {
        console.log(error.message);
      }
    );

    // this.searchID('asinayo');
  }

  goSearch() {
    this.nav.setRoot(HomePage);
    this.getEndStore.dispatch(new fromActions.GetEndAction(false));
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    console.log(page.tag);
    this.selectStore.dispatch(new fromActions.SelectAction(page.tag));
    
    this.nav.setRoot(page.component);
  }

  getItems(ev) {
    this.searchTagWord = ev.target.value;

    this.pages = SearchUtil.searchTagList(this.searchTagWord, this.tempPages);
  }

  tagSort(flag) {
    switch(flag) {
      case 1:
      {
        console.log('big');
        this.sortFlag = -1;
      }
      break;
      case 2:
      {
        console.log('small');
        this.sortFlag = 1;
      }
      break;
      case 3:
      this.sortFlag = 0;
      break;
      default:
      this.sortFlag = -1;
      break;
    }

    this.realTagSort(this.sortFlag);
  }

  realTagSort(flag: number) {

    if(flag == 0) {
      this.pages = SearchUtil.searchTagList(this.searchTagWord, this.tempPages);
      return;
    }

    this.pages.sort((a: any, b: any) => {
            
      if(a.count > b.count) {
        return 1 * flag;
      } else {
        return -1 * flag;
      }

    });
  }

}



