import { Component, AfterContentInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { PostState, SelectState, ModeState } from '../../app/ng/reducers/states';
import { Observable } from 'rxjs/Observable';
import { PostInfo } from '../../app/ng/models/model';
import * as PostReducer from '../../app/ng/reducers/post.reducer';
import * as SelectReducer from '../../app/ng/reducers/select.reducer';
import * as fromActions from '../../app/ng/actions/actions';

import * as SearchUtil from '../../app/commons/SearchUtil';
import { SafeResourceUrl, DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'page-music',
  templateUrl: 'music.html',
  styleUrls: ['/src/pages/music/music.scss']
})
export class MusicPage implements AfterContentInit {
  selectedItem: any;
  icons: string[];
  items: Array<{ title: string, note: string, icon: string }>;

  postInfoList: Observable<PostInfo[]>;
  selectTemp: Observable<String>;

  postList: PostInfo[];
  selectTag: String = 'all_all';

  tempPostList: PostInfo[];

  mdsList: Array<SearchUtil.MusicData> = [{"addr":"https://www.youtube.com/embed/wYeMe6_ISKc","type":"yb"},{"addr":"https://www.youtube.com/embed/cdp0WQCwKYo","type":"yb"},{"addr":"https://www.youtube.com/embed/j986l08Zbi0","type":"yb"},{"addr":"https://www.youtube.com/embed/cIriwVhRPVA","type":"yb"}]
  ;

  mds: Array<SearchUtil.MusicData> = new Array();

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private postStore: Store<PostState>,
    private selectStore: Store<SelectState>,
    private modetore: Store<ModeState>,
    public sanitizer: DomSanitizer) {
    // If we navigated to this page, we will have an item available as a nav param
    // this.selectedItem = navParams.get('item');

    if (this.postStore == null || this.postStore == undefined) {
      postStore = this.navParams.get('postStore');
      selectStore = this.navParams.get('selectStore');
    }

    this.mds.push({
      addr: 'https://w.soundcloud',
      type: 'sc'
    });

    this.mds.push({
      addr: 'https://youtu.be',
      type: 'yb'
    });

    this.mds.push({
      addr: 'https://www.youtube.com',
      type: 'yb'
    });

    // console.log(postStore);

    this.postInfoList = postStore.select(PostReducer.getPosts);
    this.selectTemp = selectStore.select(SelectReducer.getSelect);

    this.postInfoList.subscribe(
      (posts: PostInfo[]) => {
        // console.log(posts);
        // this.dataSource = new MatTableDataSource(probes);
        // this.dataSource.sort = this.sort;
        // console.log(ttt);
        this.tempPostList = posts;
        this.postList = SearchUtil.searchList(this.searchWord, this.tempPostList);;

        this.parseMusic2();
        // if(posts.length > 0) {
        //   if(posts[0].postList.length > 0) {
        //     console.log(JSON.stringify(posts[0].postList[0]));
        //   }

        // }

      },
      (error: any) => {
        console.log(error.message);
      }
    );

    this.selectTemp.subscribe(
      (st: String) => {
        // console.log(posts);
        // this.dataSource = new MatTableDataSource(probes);
        // this.dataSource.sort = this.sort;
        console.log(st);
        this.selectTag = st;
        // console.log(this.selectTag);

        // let posts: PostInfo[] = [];
        // posts.push(this.postList[0]);
        // this.postStore.dispatch(new fromActions.PostAction(posts));
        // console.log(this.selectTag);
      },
      (error: any) => {
        console.log(error.message);
      }
    );

  }

  parseMusic2()  {


    let mdss: Array<SearchUtil.MusicData> = new Array();

    for (let i = 0; i < this.postList.length; ++i) {

      let postOne: any = this.postList[i];

      
      let postOnList = postOne.tagMap.get(this.selectTag);

        for (var ii = 0; ii < postOnList.length; ++ii) {
          let tmds: Array<SearchUtil.MusicData> = SearchUtil.searchAddress(postOnList[ii], this.mds);
          // console.log(tmds);
          if(tmds.length > 0) {
            mdss.push.apply(mdss, tmds);
          }
          
        }
        
     

    }

    

    this.mdsList = mdss;

  }

  mediaUrl(url) {
    // console.log(url);
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

  parseMusic() {

    // title votes date replys url
    let musicList: any[] = [];

    let mdss: Array<SearchUtil.MusicData> = new Array();

    for (let i = 0; i < this.postList.length; ++i) {
      let postOne: any = this.postList[i];

      let postOnList = postOne.tagMap.get(this.selectTag);

        for (var ii = 0; ii < postOnList.length; ++ii) {
          let post = postOnList[ii];
          // let yIdx: number = post.body.indexOf('youtube.com');
          // if (yIdx < 0) {
          //   continue;
          // }

          // let first: number = post.body.lastIndexOf('"', yIdx);
          // let last: number = post.body.indexOf('"', yIdx);

          // let url: string = post.body.substring(first+1, last);

          // console.log(url);

          let retMds: Array<SearchUtil.MusicData> = SearchUtil.searchAddress(post.body, this.mds);

          // if( url != null && url != null && url != '') {
          //   mdss.push({
          //     addr : url,
          //     type: 'yb',
          //     post: post
          //   });
          // }
          
        }
    }

    // console.log(JSON.stringify(mdss));

    this.mdsList = mdss;

  }

  optionSelected(event) {
    console.log('-=-=-=-=optionSelected=-=-=-=-=-=');
    console.log(event);
    this.modetore.dispatch(new fromActions.ModeAction(String(event)));
  }

  onCancel(event) {

    // console.log('-=-=-=-=onCancel=-=-=-=-=-=');
    // console.log(event);

  }

  ngAfterContentInit() {

  }

  searchWord = '';

  getItems(ev) {

    this.searchWord = ev.target.value;

    this.postList = SearchUtil.searchList(ev.target.value, this.tempPostList);
    this.parseMusic2();
  }

}




