import { Component, AfterContentInit } from '@angular/core';
import { NavController, Loading } from 'ionic-angular';

import { PostInfo, AuthorInfo, UserInfo } from '../../app/ng/models/model';
import { Observable } from 'rxjs/Observable';
import { Store } from '@ngrx/store';
import { PostState, AuthorState, SelectState, GetEndState } from '../../app/ng/reducers/states';

import * as PostReducer from '../../app/ng/reducers/post.reducer';
import * as AuthorReducer from '../../app/ng/reducers/author.reducer';
import * as fromActions from '../../app/ng/actions/actions';

import * as steem from "steem";

import { LoadingController } from 'ionic-angular';
import { ListPage } from '../list/list';

import * as _ from 'lodash';

import { AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements AfterContentInit{

  constructor(public navCtrl: NavController,
    private postStore: Store<PostState>,
    private authorStore: Store<AuthorState>,
  private selectStore: Store<SelectState>,
  private getEndStore: Store<GetEndState>,
  public loadingCtrl: LoadingController,
  public alertCtrl: AlertController,
  private storage: Storage) {
    this.postInfoList = postStore.select(PostReducer.getPosts);
    this.authorInfoList = authorStore.select(AuthorReducer.getAuthor);

    HomePage.sauthorStore = this.authorStore;
    HomePage.spostStore = this.postStore;
    HomePage.snavCtrl = this.navCtrl;
    HomePage.sgetEndStore = this.getEndStore;

  }

  ngAfterContentInit() {


    // this.storage.set('histories', ['asinayo']);

    // const _this = this;
    this.storage.get('histories').then((val) => {

      if(val == null) {
        this.storage.set('histories', []);
        this.histories = [];
      } else {
        this.histories = val;
      }
      // console.log('histories', val);
    })
    // .catch((err) => {
    //   console.log(err);
    // });

    // this.searchID('asinayo');
  }


  steemitid: String;

  selectTag: String = 'all_all';

  static DATE = new Date().toISOString().split('.')[0];
  static GET_SIZE = 10;

  postInfoList: Observable<PostInfo[]>;
  authorInfoList: Observable<AuthorInfo[]>;
  static spostStore: Store<PostState>;
  static sauthorStore: Store<AuthorState>;
  static sgetEndStore: Store<GetEndState>;
  static sid: String;
  selectedPosts: Array<any> = new Array();

  postList: PostInfo[];

  static loader: Loading;
  static snavCtrl: NavController;
  static ll: boolean = true;

  histories: UserInfo[];

  search() {
    console.log(this.steemitid);
    
    
    if(this.steemitid === undefined
       || this.steemitid === null
       || this.steemitid.length <= 0
       || this.steemitid == '') {

        this.showAlert('실수하신거 같은데 보고 싶은 아이디를 입력해야 정신건강에 좋습니다.');
         return;
       }

    
    this.searchID(this.steemitid);

    

    
    
  }

  showAlert(msg: string) {
    let alert = this.alertCtrl.create({
      title: '경고!',
      subTitle: msg,
      buttons: ['응']
    });
    alert.present();
  }

  searchHistory(his: UserInfo) {
    this.searchID(his.user);
  }


  searchID(searchWord: String) {

    HomePage.loader = this.loadingCtrl.create({
      content: "Please wait...",
      // duration: 3000
    });
    HomePage.loader.present();

    let check: boolean = false;
    for(let ii = 0 ; ii < this.histories.length; ++ii) {
      if(this.histories[ii].user == searchWord) {
        this.histories[ii].count += 1;
        check = true;
      }
    }

    if(check == false) {
      this.histories.push({
        avatar: null,
        user:searchWord.toString(),
        count:1
      });
    }

    this.histories.sort((a: UserInfo, b: UserInfo) => {

      if ( a.count > b.count ) {
        return -1;
      } else {
        return 1;
      }
    });

    this.storage.set('histories', this.histories);

    HomePage.ll = true;

    HomePage.sid = searchWord;
    // this.loading.push("Loading")
    // MyApp.getAuthorInfo(searchWord);

    let pi: PostInfo = {
      postList: new Array(),
      tagMap: new Map(),
      tagList: new Array(),
      catchMap: new Map()
    }

    HomePage.getNextPosts2(searchWord, null, pi);
    const bb: Boolean = true;
    HomePage.sgetEndStore.dispatch(new fromActions.GetEndAction(bb));

  }

  static getAuthorInfo(searchWord: String) {
    steem.api.getAccounts([searchWord], function (err, response) {

      let ai: AuthorInfo = {};

      if (err != undefined) {
        console.log(err);
      } else {
        ai.author = response[0];

        if (ai.author.json_metadata.length <= 0 || ai.author.json_metadata == undefined) {
          ai.meta = {
            profile: {
              name: ai.author.name,
            }
          }
        } else {
          ai.meta = JSON.parse(ai.author.json_metadata);
        }


        // console.log(ai.author);

        ai.author.last_post = HomePage.convertDate(ai.author.last_post);

        let arr: Array<AuthorInfo> = new Array();
        arr.push(ai);
        // console.log(arr);
        HomePage.sauthorStore.dispatch(new fromActions.AuthorAction(arr));
      }


    });
  }

  static getNextPosts2(author: String, startPermlink: any, pi: PostInfo) {

    // console.log(startPermlink + " -- " +date );
    steem.api.getDiscussionsByAuthorBeforeDateAsync(author, startPermlink, HomePage.DATE, HomePage.GET_SIZE, function (err, result) {
      // console.log(result);

      if(err != null || err != undefined) {
        return;
      }

      var post = null;
      var lastPermlink: any;
      for (var indexI = 0; indexI < result.length; ++indexI) {
        post = result[indexI];
        lastPermlink = post.permlink;

        if (indexI + 1 < result.length || result.length < HomePage.GET_SIZE ) {
          // console.log(post.author + " --- " + post.root_title);
          pi.postList.push(post);
        }

      }

      if (result.length >= HomePage.GET_SIZE || false) {
        // console.log(lastPermlink);

        let ppi: PostInfo = _.clone(pi);

        HomePage.sortTag2(ppi);

        HomePage.getNextPosts2(author, lastPermlink, pi);
        
      } else {
        // for (var aa = 0; aa < pi.postList.length; ++aa) {
        //   // console.log(pi.postList[indexI]);
        //   console.log(pi.postList[aa].author + " --- " + pi.postList[aa].root_title);
        // }
        HomePage.sortTag2(pi);
      }
    });

  }

  static replaceAll = function(str, org, dest) {
    return str.split(org).join(dest);
}




  static convertDate(date: any): String {
    let index = String(date).indexOf('T');
    if(index < 0) {
      return date;
    }
    let retDate = String(date).substring(0, index);
    return retDate;
  }

  static sortTag2(pi: PostInfo) {

    var post: any = null;

    pi.tagMap = new Map();
    pi.tagList = new Array();
    pi.tagMap.set('all_all', new Array());
    pi.tagList.push('all_all');


    for (var indexI = 0; indexI < pi.postList.length; ++indexI) {
      post = pi.postList[indexI];

      pi.tagMap.get('all_all').push(post);

      post.created = HomePage.convertDate(post.created);
      // post.created = post.created.split("T")[0];
      try {
        post.json_metadata = JSON.parse(post.json_metadata);
      } catch(e) {

      }
      

      var tags = post.json_metadata.tags;

      for (var indexJ = 0; indexJ < tags.length; ++indexJ) {

        if (pi.tagMap.get(tags[indexJ]) === undefined) {
          pi.tagMap.set(tags[indexJ], new Array());
          pi.tagList.push(tags[indexJ]);
        }
        pi.tagMap.get(tags[indexJ]).push(post);

      }

    }

    // console.log('END');
    
    let arr: Array<PostInfo> = new Array();
    arr.push(pi);
    // console.log(arr);
    HomePage.spostStore.dispatch(new fromActions.PostAction(arr));
    
    // BlogComponent.srouter.navigateByUrl('/blog/'+BlogComponent.sid);

    if(HomePage.ll) {
      HomePage.ll = false;
      console.log('close loading!!!');
      HomePage.loader.dismissAll();
    }
    

    // HomePage.snavCtrl.push(ListPage, {
    //   postStore: HomePage.spostStore,
    //   selectStore: HomePage.sauthorStore
    // });

  }

}



